:root {
  --accent-color: #00acdc;
  --main-text-color: #616070;
  --accent-text-color: #060b35;
  --white: #fff;
  --border-color: #f3f3f3;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 500;
}

ul {
  list-style: none;
}

.header {
  box-sizing: content-box;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  align-self: center;
  display: block;
}

.nav__list {
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: var(--accent-text-color);
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: inline-flex;
  position: relative;
}

.nav__link:hover {
  color: var(--accent-color);
}

.main {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.card {
  background: var(--white);
  border: 1px solid var(--border-color);
  color: var(--accent-text-color);
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  font-weight: 400;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  margin-top: 40px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: var(--main-text-color);
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 1.4;
}

.card__rate {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__counter {
  align-self: flex-end;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__price-title {
  color: var(--main-text-color);
  font-size: 12px;
  line-height: 1.5;
}

.card__price-value {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  box-sizing: border-box;
  background: var(--accent-color);
  color: var(--white);
  text-transform: uppercase;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.14;
  text-decoration: none;
  transition: background-color .3s, color .3s;
  display: flex;
}

.card__buy-button:hover {
  border-color: var(--accent-color);
  background-color: var(--white);
  color: var(--accent-color);
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--4 .stars__star:nth-child(-n+1), .stars--4 .stars__star:nth-child(-n+2), .stars--4 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.is-active {
  color: var(--accent-color);
}

.is-active:after {
  content: "";
  background-color: var(--accent-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}
